import { createRouter, createWebHistory } from 'vue-router'
import LoginForm from '../views/LoginForm.vue'

// –ò–º–ø–æ—Ä—Ç–∏—Ä—É–µ–º –∫–æ–º–ø–æ–Ω–µ–Ω—Ç—ã –¥–ª—è –º–∞—Ä—à—Ä—É—Ç–∏–∑–∞—Ü–∏–∏
const Schedule = () => import('../views/Schedule.vue')
const Grades = () => import('../views/Grades.vue')
const Homework = () => import('../views/Homework.vue')
const ManageLesson = () => import('../views/ManageLesson.vue')
const AdminPanel = () => import('../views/AdminPanel.vue')

const routes = [
  {
    path: '/',
    redirect: '/login'
  },
  {
    path: '/login',
    name: 'Login',
    component: LoginForm,
    meta: { 
      title: '–í—Ö–æ–¥ –≤ —Å–∏—Å—Ç–µ–º—É',
      public: true
    }
  },
  {
    path: '/schedule',
    name: 'Schedule',
    component: Schedule,
    meta: { title: '–†–∞—Å–ø–∏—Å–∞–Ω–∏–µ' }
  },
  {
    path: '/grades',
    name: 'Grades',
    component: Grades,
    meta: { title: '–û—Ü–µ–Ω–∫–∏' }
  },
  {
    path: '/homework',
    name: 'Homework',
    component: Homework,
    meta: { title: '–î–æ–º–∞—à–Ω–∏–µ –∑–∞–¥–∞–Ω–∏—è' }
  },
  {
    path: '/manage-lesson',
    name: 'ManageLesson',
    component: ManageLesson,
    meta: { 
      title: '–£–ø—Ä–∞–≤–ª–µ–Ω–∏–µ —É—Ä–æ–∫–∞–º–∏',
      requiresTeacher: true 
    }
  },
  {
    path: '/admin',
    name: 'AdminPanel',
    component: AdminPanel,
    meta: { 
      title: '–ê–¥–º–∏–Ω –ø–∞–Ω–µ–ª—å',
      requiresAdmin: true 
    }
  }
]

const router = createRouter({
  history: createWebHistory(),
  routes
})

router.beforeEach((to, from, next) => {
  const isAuthenticated = !!localStorage.getItem('userRole')
  
  // –£—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ–º –∑–∞–≥–æ–ª–æ–≤–æ–∫ —Å—Ç—Ä–∞–Ω–∏—Ü—ã
  document.title = to.meta.title || '–£—á–µ–±–Ω—ã–π –ø–æ—Ä—Ç–∞–ª'
  
  if (!isAuthenticated && !to.meta.public) {
    // –ü–µ—Ä–µ–Ω–∞–ø—Ä–∞–≤–ª—è–µ–º –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü—É –ª–æ–≥–∏–Ω–∞
    next('/login')
  } else if (isAuthenticated && to.path === '/login') {
    // –ï—Å–ª–∏ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å –∞–≤—Ç–æ—Ä–∏–∑–æ–≤–∞–Ω –∏ –ø—ã—Ç–∞–µ—Ç—Å—è –∑–∞–π—Ç–∏ –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü—É –ª–æ–≥–∏–Ω–∞
    next('/schedule')
  } else {
    next()
  }
})

export default router 